// "use client"

import React,{ useContext } from "react";

import Link from "next/link";
import ApiContext from "../../Context/ProductApi/ApiContext";
import { wixClientServer } from "@/lib/wixClientServer";




const ProductPage = async ({categoryId}) => {
    console.log("Received Category ID:", categoryId); // Debugging
    if (!categoryId) {
        return { error: "No category ID provided" };
    }
    const wixClient = await wixClientServer();
    const res = await wixClient.products.queryProducts().eq("collectionIds",[categoryId]).find();
    // console.log(res.items.media.items)

    return(
        <section className="product_page">
            <div className="container">
                <h2 className="title">Featured Product</h2>
                <div className='inner_'>
                    <ul className="product_list">
                    {res.items.map((product,index)=>{
                        return(
                        <li className="s" key={index}>
                            <Link href={"/"+ product.slug}>
                            <div className='img_wrap'>
                                <img src={product.media?.items[1]?.image?.url} />
                            </div>
                            <label className='cat_name'>{product.name}</label>  
                            </Link>
                        </li>
                        )
                    })}
                    </ul>
                    </div>
            </div>
        </section>
    )
};
export default ProductPage;  